.contentWrapper {
  position: relative;
  width: 100%;
  margin: 0 auto;
  /* background: #e6e6e6; */
  color: #545454;
  border-radius: 8px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15px;

  font-size: 18px;
}
.title {
  margin: 10px 0;
  /* padding-bottom: 10px; */
  width: 100%;
  color: #78788c;
  border-bottom: 2px solid #78788c;
  font-size: 24px;
}
.text {
  margin: auto;

  color: #78788c;

  font-size: 17px;
}
